		// 		}
		// 	}
		// }

		.contact_form {
			width: 60%;
			padding-left: 5vw;
			opacity: 0;
			transition: 0.7s 0.7s;

			form {
				height: 100%;
				background: #fff;
				border: 1px solid #33475b;
				padding: 50px;
				border-radius: 1.25em;
			}

			table {
				width: 100%;
				tr {
					th {
						display: block;
						font-size: 16px;
						font-weight: 400;
						width: 100%;
						// height: 60px;
						// padding: 10px 0;
						// padding-right: 20px;
						font-family: 'Verdana', sans-serif;
						text-align: left;
					}
					td {
						display: block;
						// height: 60px;
						width: 100%;
						// padding: 10px 0;
						padding-top: 10px;
						margin-bottom: 20px;
						font-family: 'Verdana', sans-serif;
						text-align: left;

						p {
							font-size: 14px;
							display: block;
							width: auto;
							font-weight: 500;
							line-height: 18px;
							padding-top: 0;
							margin: 4px 0;
							color: #f2545b;
						}

						input,
						select {
							background: #f5f8fa;
							display: inline-block;
							width: 100%;
							height: 40px;
							padding: 9px 10px;
							font-size: 16px;
							line-height: 22px;
							color: #33475b;
							border: 1px solid #cbd6e2;
							box-sizing: border-box;
							border-radius: 10px;
							font-family: 'Verdana', sans-serif;
						}

						textarea {
							background: #f5f8fa;
							resize: none;
							width: 100%;
							height: 80px;
							padding: 9px 10px;
							font-size: 16px;
							line-height: 22px;
							color: #33475b;
							border: 1px solid #cbd6e2;
							box-sizing: border-box;
							border-radius: 2px;
							font-family: 'Verdana', sans-serif;
						}
						input[type='checkbox'],
						input[type='radio'] {
							width: auto;
							height: auto;
							padding: 0;
							margin: 3px 5px 3px 10px;
							line-height: normal;
							border: none;
						}

						// input[type='checkbox'] + label,
						// input[type='radio'] + label {
						// 	display: inline-block;
						// 	height: 60px;
						// 	line-height: 60px;
						// }
					}
				}
				.checkboxList {
					li {
						margin-bottom: 10px;
					}
				}
				.form_btn {
					td {
						// display: flex;
						// justify-content: center;
						// width: 100%;
						text-align: center;
						padding: 30px 0 0;
					}

					input {
						border: 0;
						width: 40%;
						height: 40px;
						font-size: 16px;
						line-height: 22px;
						color: #33475b;

						box-sizing: border-box;
						border-radius: 2px;
						font-family: 'Verdana', sans-serif;
						cursor: pointer;

						&[type='submit'] {
							margin-left: 20px;
							background: #cbd6e2;
							border: 1px solid #cbd6e2;
						}
						&[type='reset'] {
							background: #cbcbcb;
							border: 1px solid #cbd6e2;
						}
					}
				}
			}
		}
	}

	&.on {
		.head_title__ex {
			opacity: 1;
			transform: translateY(0);
		}
		.contact_form {
			opacity: 1;
		}
	}

	@media screen and (max-width: $tablet) {
		> .inner {
			display: block;

			.contact_head {
				width: 100%;
			}

			.contact_form {
				width: 100%;
				margin-top: 80px;
				padding: 50px 0 0 0;
				border-top: 1px solid #080808;
				border-left: none;

				form {
					padding: 40px 30px;

					table {
						th {
							vertical-align: top;
							padding-top: 18px;
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: $mobile) {
		> .inner {
			.contact_form {
				margin-top: 60px;
				padding: 30px 0 0 0;

				form {
					padding: 20px 30px;
				}
			}
		}
	}
}
